import { APP_CONFIG, IAppConfig } from './../../../config';
import { Injectable, Inject, Output, EventEmitter } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject, Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { User } from '../models/user';
@Injectable({ providedIn: 'root' })
export class AuthenticationService {

  @Output() getLoggedInUser: EventEmitter<any> = new EventEmitter();

  private currentUserSubject: BehaviorSubject<User> =
    new BehaviorSubject<User>(JSON.parse(sessionStorage.getItem('currentUser')));
  public currentUser: Observable<User> =
    this.currentUserSubject.asObservable();
  constructor(private http: HttpClient) { }
  login(u: User) {
    return this.http.post<any>('api/user/authenticate', u)
      .pipe(map(user => {
        // login successful if there's a jwt token in the response
        if (user && user.loggedIn) {
          // store user details and jwt token in local storage to keep user logged in between page refreshes
          sessionStorage.setItem('currentUser', JSON.stringify(user));
          this.currentUserSubject.next(user);

          this.getLoggedInUser.emit(user);

          return true;
        }
        else {
          sessionStorage.removeItem('currentUser');
          this.currentUserSubject.next(null);

          this.getLoggedInUser.emit(null);

          return false;
        }
      }));
  }
  logout() {
    // remove user from local storage to log user out
    sessionStorage.removeItem('currentUser');
    this.currentUserSubject.next(null);

    this.getLoggedInUser.emit(null);
  }
}
